<template>
  <div class="caregory">
    <ul class="lists">
      <li v-for="list in lists" :key="list.id" >

        <router-link :to="{name:'userDetails',params:{userName:list.author.loginname}}"><span class="head"><img :src="list.author.avatar_url" alt=""></span></router-link>
        
        <router-link style="width:1150px;padding-left:20px;" :to="{name:'details',params:{topicId:list.id}}">
          {{ list.title }} <span>{{ list.tab }}</span>
        </router-link>
      </li>
    </ul>

  </div>
</template>

<script>
import {
  getclientHeight,
  getscrollHeight,
  getscrollTop,
} from "../scroll/scroll";
export default {
  props: ["tab","id"],

  data() {
    return {
      lists: [],
      loading: true,
      page: 1,
    };
  },
  watch: {
    tab: {
      async handler(newValue) {
        const res = await this.$http.get(
          `/topics?tab=${newValue ? newValue : "all"}`
        );

        this.lists = res.data;
      },
      immediate: true,
    },
  },
  methods: {
    async load() {
      if (this.lists.length) {
        const scrollBottm = Math.abs(
          getscrollTop() - getscrollHeight() + getclientHeight()
        );
        if (scrollBottm <= 20) {
          if (this.loading) {
            this.loading = false;
            this.page++;
            const newLists = await this.$http.get(
              `/topics?tab=${this.tab ? this.tab : "all"}&page=${this.page}`
            );
            this.lists = [...this.lists, ...newLists.data];
            this.loading = true;
          }
        }
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.load); //把load函数给window，滚动条触发load函数
  },
  destoryed() {
    window.removeEventListener("scroll", this.load); //网页关闭卸载load函数，要不然进别的页面也会有这个函数
  },
};
</script>

<style>

.lists {
  width: 100%;
}
.lists li{
  display: flex;
}
.lists li img{
  display: inline-block;
  width: 30px;
  height: 30px;
  vertical-align: middle;
}
.lists li a:first{
  width: 30px;
  text-align: center;
}
.lists li a {
  display: block;
  box-sizing: border-box;
  background-color: #fff;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
  text-decoration: none;
  color: #000;

}
.lists li a:hover{
  background-color: rgb(246, 246, 246);
  text-decoration: underline;
}
.lists li a span {
  background-color: #ccc;
  border-radius: 5px;
  padding: 0 3px;
  margin-left: 5px;
  display: inline-block;
  line-height: 20px;
}
.lists li a span:hover {
  text-decoration: none;
}
</style>